body .info,
.editor-styles-wrapper .info {
  padding: 1rem;
  background-color: #e0e5eb;
  color: #181d25;
}
body .responsive-tab-panel,
.editor-styles-wrapper .responsive-tab-panel {
  border-top: 1px solid #9ca3af;
}
body .responsive-tab-panel .components-tab-panel__tabs,
.editor-styles-wrapper .responsive-tab-panel .components-tab-panel__tabs {
  border-bottom: 1px solid #9ca3af;
}
body .responsive-tab-panel .components-tab-panel__tabs .components-tab-panel__tabs-item,
.editor-styles-wrapper .responsive-tab-panel .components-tab-panel__tabs .components-tab-panel__tabs-item {
  margin: 0;
  padding: 0 0.775rem;
  text-align: center;
}
body .responsive-tab-panel .components-tab-panel__tabs .components-tab-panel__tabs-item.active-tab,
.editor-styles-wrapper .responsive-tab-panel .components-tab-panel__tabs .components-tab-panel__tabs-item.active-tab {
  box-shadow: inset 0 var(--wp-admin-border-width-focus) var(--wp-admin-theme-color);
}

@keyframes fade-up {
  from {
    opacity: 0;
    transform: translateY(0.5rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
